<template>
	<a v-bind:href="'#' + link" class="nav-btn" v-on:click.prevent="scrollToSection">{{ link | capitalize }}</a>
</template>

<script>
export default {
	name: 'HeaderLink',
	props: {
		link: String,
	},
	methods: {
		scrollToSection: function(){
			var elem = document.getElementById(this.link);
			var coord = elem.getBoundingClientRect().top + window.scrollY;
			window.scroll({
				top:  coord,
				behavior: 'smooth'
			});
		}
	},
	filters: {
		capitalize: function(str){
			return str.substring(0,1).toUpperCase() + str.substring(1);
		}
	}
}
</script>
